<template>
  <div id="blog-header">
  <nav>
    <ul>
      <li><router-link to="/">博客浏览</router-link></li>
      <li><router-link to="/add">添加博客</router-link></li>
    </ul> 
  </nav>
  </div>
</template>

<script>


export default {
  name: 'blog-header',
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#blog-header {
  max-width: 500px;
  margin: 0 auto;
  text-align: center;
  background-color: crimson;
}

ul {
  width: 100%;
  margin: 0 auto;
}

li {
display: inline-block;
list-style: none;
padding: 10px;
}

a {
  margin: 0 auto;
  color: #fff;
  text-decoration: none;
  width: 70px;
  height: 25px;
  border-radius: 5px;
  padding: 5px;
}

.router-link-exact-active {
  background-color: rgba(255,255,255,0.8);
  color: crimson;

}

</style>



