<template>
  <div class="add-blog">
  	<h1>写博客</h1>
	  <form v-show="!sended">
	   	<p>主题</p>
	   	<input type="text" v-model="blog.title" required/>
	   	<p>内容</p>
	   	<textarea v-model="blog.content"></textarea>
	   	<p>分类</p>
	   	<div id="catalogue">
		   	<span v-for="catalogue in catalogues">
			   	<label>{{catalogue}}</label>
				<input type="checkbox" v-bind:value="catalogue" v-model="blog.catalogues">
			</span>
	   	</div>

	   	<p>作者</p>
	   	<select v-model="blog.author">
			<option v-for="author in authors">{{author}}</option>
		</select>
	   	<button v-on:click.prevent="postBlog">添加博客</button>
		</form>
		<div v-if="sended">您的博客发布成功！</div>	

		<br />
		<div class="preview">
			<p>{{blog.title}}</p>
			<p>{{blog.content}}</p>
			<p>{{blog.catalogues}}</p>
			<p>{{blog.author}}</p>
			<p>{{blog.blogId}}</p>
		</div>
  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data () {
    return {
    	"blog": {
    		title: "",
    		content: "",
    		catalogues: [],
    		author:"",
    		blogId: "2"
    	},
    	authors: ["Tal","babala","wbh"],
    	catalogues: ["积极心理学","学习之道","黑客之道"],
    	sended: false,
    }
  },
  methods: {
  	postBlog: function(){
  		this.$http.post("https://vue-blog-87ce0.firebaseio.com/blog.json",{
  			title: this.blog.title,
  			body: this.blog.content,
  			catalogues: this.blog.catalogues,
  			author: this.blog.author,
  			blogId: 1,
  		})
  		.then(function(data){
  			this.sended = !this.sended;
  			console.log(data);
  			console.log('haha');
  		})
  	}
  },
  created(){
  	this.$http.get('https://vue-blog-87ce0.firebaseio.com/blog.json')
  	.then(function(data){
  		console.log(data.body.length);
  	})
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--
1/ 
 -->
<style scoped>
h1 {
	text-align: center;
}

.add-blog {
	max-width: 500px;
	margin: 10px auto;
	padding: 20px;
}

.add-blog p {
	text-align: center;
}

input[type="text"],select,textarea {
	display: inline-block;
	width: 100%;
}

textarea {
	height: 200px;
}

#catalogue {
	width: 100%;
}

#catalogue span {
	display: inline-block;
	width: 33%;
	text-align: center;
}

button {
	display: block;
	width: 100px;
	height: 30px;
	background-color: crimson;
	color: #fff;
	font-size: 16px;
	border: 0;
	margin: 20px auto;
	text-align: center;
	border-radius: 5px;
	cursor: pointer;
}

</style>



