<template>
  <div id="show-blogs">
  	<h1>{{title}}</h1>
  	<input type="text" v-model="search"/>
  	<div v-for="article in filterArticles" class="article">
	  	<router-link v-bind:to="'/blog/' + article.blogId"><h5 v-rainbow>{{article.title}}</h5></router-link>
	  	<p v-preview>{{article.body}}</p>
  	</div>
  </div>
</template>

<script>
export default {
  name: 'show-blogs',
  data() {
  	return {
  		title: "文章列表",
  		articles: [],
  		search: ""
  	}
  },
  created(){
  	this.$http.get("https://vue-blog-87ce0.firebaseio.com/blog.json")
  	.then(function(data){
      let body = data.body;
      for(let key in body){
        this.articles.push(body[key]);
      }
  	})
  },
  directives: {
  	preview: {
  		bind: function(el,binding,vnode){
  			el.innerHTML = el.innerHTML.slice(0,50) + '...';
  		}
  	}
  },
  computed: {
  	filterArticles(){
  		return this.articles.filter((article) =>  article.title.match(this.search));
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
	text-align: center;
}
#show-blogs {
	max-width: 500px;
	margin: 0 auto;
}

input {
	display: inline-block;
	width: 100%;
	height: 25px;
	box-sizing: border-box;
}

.article {
	margin-top: 10px;
	background: #dddddd;
	padding: 5px;
}

a {
	text-decoration: none;
}
</style>



