<template>
	<div id="single-blog">
		<h2>{{blog.title}}</h2>
		<p>{{blog.body}}</p>
	</div>
</template>


<script>
	export default {
		name: "single-blog",
		data(){
			return {
				id: this.$route.params.id,
				blog: {}
			}
		},
		created(){
			this.$http.get('https://vue-blog-87ce0.firebaseio.com/blog.json')
			.then(function(data){

				for(let key in data.body){
					if (data.body[key].blogId == this.id)
						this.blog = data.body[key];
				}				
			})
		}
	}

</script>
<style scoped>
#single-blog {
	max-width: 500px;
	margin: 10px auto;	
	padding: 5px;
	background-color: #eee;
	box-sizing: border-box;
}
</style>




